import {mapState} from 'vuex'
export default {
	data() {
		return {
			currentIndex:0
		};
	},
	mounted() {
		this.getCategoryList()
	},
	methods:{
		getCategoryList(){
			this.$store.dispatch('getCategoryList')
		}
	},
	computed:{
		...mapState({
			categoryList: state => state.category.categoryList
		}),
		
		// 计算当前点击的一级分类
		currentCategory1(){
			return this.categoryList[this.currentIndex] || {}
		}
	}
}
